import * as React from 'react'
import Bold from './bold'
import Italic from './italic'
import Underline from './Underline'
import Color from './color'
import backgroundColor from './background'
import Strikethrough from './strikethrough'
import Left from './left'
import Center from './center'
import Right from './right'
import Ul from './ul'
import Ol from './ol'
import Hyperlink from './hyperlink'
import Unhyperlink from './unhyperlink'

// 
const toolCollections = [
  { type: 'bold', title: '', widget: Bold },
  { type: 'italic', title: '', widget: Italic },
  { type: 'underline', title: '', widget: Underline },
  { type: 'strikethrough', title: '', widget:  Strikethrough},
  { type: 'color', title: '', widget: Color },
  { type: 'backgroundColor', title: '', widget: backgroundColor },
  { type: 'alignLeft', title: '', widget: Left },
  { type: 'alignCenter', title: '', widget: Center },
  { type: 'alignRight', title: '', widget: Right },
  { type: 'ul', title: '', widget: Ul },
  { type: 'ol', title: '', widget: Ol },
  { type: 'hyperlink', title: '', widget: Hyperlink },
  { type: 'unhyperlink', title: '', widget: Unhyperlink },
]

export default class ToolFactory {
  /**
   * tool
   * @param {*} name 
   * @param {*} props 
   */
  static getTool(type, props = {}) {
    const tools = toolCollections.filter((tool) => {
      if (tool.type === type) {
        return true
      }
    })

    if (tools.length > 0) {
      return React.createElement(tools[0].widget, {
        ...props,
        key: tools[0].type,
        type: tools[0].type,
        title: tools[0].title
      })
    }

    return null
  }
}

export { toolCollections }


